import * as React from 'react';
import {styles} from './preset-line.module.scss';
import {PRESETS} from "../../../config";


export const PresetLine = ({setPresetState ,presetState } ) => {
  const removeElem = (i)=>{
      if(!window.localStorage.getItem('presetList')){
        !window.localStorage.setItem('presetList', JSON.stringify(PRESETS));
      }
    const memoryItem = JSON.parse(window.localStorage.getItem('presetList'));
    memoryItem.splice(i,1);
    window.localStorage.setItem('presetList' ,JSON.stringify(memoryItem) )
    setPresetState(memoryItem);
  }

    const defaultsHandler= ()=>{
      window.localStorage.setItem('presetList' ,JSON.stringify(PRESETS) );
      setPresetState(PRESETS)
    }


   const presetHandler = (presetList)=>{
    const actions = [];

    presetList.forEach(singlePreset=>{
      const {name , r,g,b ,secondParam } =singlePreset;
      const action = ()=>{
        window.VmtHelper['remove'+name]();
        window.VmtHelper['apply'+name](r, g, b, window.makeUpAlpha , secondParam);
      }
      actions.push( action );
      action();
    })
     window.lastChanges=()=>{
      actions.forEach((a)=>{a()});
     }
  }
  return (
    <div className={styles}>
      <div className="wrap-img">
        {presetState.map((preset , i )=>{
          return(
            <div  key={i} className={'wrap'}>
              <button data-govno={i} onClick={()=>{removeElem(i)}} className={'delete'}> delete preset <img src="/delete-icon.png" alt="delete"/> </button>
              <button  onClick={ ()=>{ presetHandler(preset.presetList)}}> <img src={preset.img} /> preset </button>
            </div>
          )
        })}
        <button onClick={defaultsHandler} >  <img src="/defaults-icon.png"  /> defaults </button>
      </div>
    </div>
  );
};
