import * as React from 'react';
import {useMemo} from 'react';
import {styles} from './colors.module.scss';
import RemoveImg from '../../../images/remove.png'
import {lastChangeSaver, presetSaver} from "./savers";


const line = (colors, api) => {

  return <>
    <button className="color-btn" onClick={() => {
      window.VmtHelper['remove' + api]();

      window.lastChanges = {
        ...window.lastChanges, [api]: () => {
        }
      }
    }}>
      remove
      <img src={RemoveImg}/>
    </button>
    {colors.map((rgb, i) => {
      const {r, g, b, secondParam} = rgb;
      return <button
        key={i}
        style={{backgroundColor: `rgba(${r},${g},${b}`}}
        className="color-btn"
        onClick={() => {
          lastChangeSaver({api,r,g,b,secondParam})
          presetSaver({api,r,g,b,secondParam})
          window.VmtHelper['remove' + api]();
          window.VmtHelper['apply' + api](r, g, b, window.makeUpAlpha, secondParam);
        }}
      > set color</button>
    })}
  </>

}

export const Colors = ({api, colors, alpha}) => {
  const Result = useMemo(() => line(colors, api, alpha), [alpha])
  return <div className={styles}>
    <div className="btn-line">
      {Result}
    </div>
  </div>;
};

