export const BackBtn = ({link , external}) => {
  const back =(e)=>{
    e.preventDefault();
    navigate(-1);
  }
      if(!link){
        return <a className={styles} onClick={back} href={'#'}>
          <Arrow/>
        </a>
      }
  return (
    <>
      {external?   <a className={styles} href={link}><Arrow/> </a> :
        <AniLink  className={styles} swipe direction="right" hex="#663399"  entryOffset={80}  duration={1} to={link}>
          <Arrow/>
        </AniLink>
       }
    </>

      

  );
};
