const add = (src)=>{
  const s = document.createElement('script');
  s.src = src;
  document.body.append(s);
  const promise  = new Promise((resolve, reject) => {
    s.addEventListener('load', resolve);
    s.addEventListener('error', resolve);
  });
  return promise
}

 export  const jsloader =( arr=[]  )=>{
   let i = 0;
   const loop = ( )=>{
     if(i>arr.length-1) return;
     add(arr[i]).then(()=>{
       i++;
       loop();
     })
   }
   loop();
 }



 export const inlineJs = (arr)=>{
       if(document.querySelector('.inlineJsAdded')){return};
       arr.forEach((t , i)=>{
         setTimeout(()=>{
           const   s = document.createElement('script');
           s.innerHTML=t;
           document.body.append(s);
         }, i )

       });
   document.body.classList.add('inlineJsAdded');
}



 export const inlineJsType = (arr)=>{
       if(document.querySelector('.inlineJsTypeAdded')){return};
       arr.forEach((item , i)=>{
         setTimeout(()=>{
           const   s = document.createElement('script');
           s.innerHTML=item.text;
           s.id=item.id;
           s.type=item.type;
           document.querySelector('.fragmentPlace').append(s);
         }, i )

       });
   document.body.classList.add('inlineJsTypeAdded');
}


