import {PRESETS} from "../../../config";

export const photoZoomIn=()=>{
  const  canvas1 = document.getElementById('tryon-live-canvas-0');
  const  canvas2 = document.getElementById('tryon-live-canvas-1');
  window.beautyScale=window.beautyScale+0.1
  canvas1.style.width=  canvas1?.width *window.beautyScale+'px';
  canvas2.style.width=  canvas2?.width *window.beautyScale+"px";
}
export const  photoZoomOut=()=>{
  const  canvas1 = document.getElementById('tryon-live-canvas-0');
  const  canvas2 = document.getElementById('tryon-live-canvas-1');
  if(window.beautyScale>0.1) window.beautyScale=window.beautyScale-0.1 ;
  canvas1.style.width=  canvas1?.width *window.beautyScale+'px';
  canvas2.style.width=  canvas2?.width *window.beautyScale+"px";
}

export const liveZoomIn=()=>{
  window.beautyScale=window.beautyScale+0.1
}    ;
export const liveZoomOut=()=>{
  if(window.beautyScale>0.7) window.beautyScale=window.beautyScale-0.1 ;
}



export  const savePresetHandler = (setPresetState)=>{
  if(window.presetSaver){
    const canvas = document.querySelector('#tryon-live-canvas-1');
    const dataURL = canvas.toDataURL("image/webp", 0.2);
    const presetList = [];
    for( let line in window.presetSaver){
      presetList.push(window.presetSaver[line]);
    }
    const list = JSON.parse(window.localStorage.getItem('presetList'))||[...PRESETS];

    if(list.length>5){
      list[5]={img:dataURL , presetList}
    } else  {
      list.push( {img:dataURL , presetList});
    }
    setPresetState(list);
    window.localStorage.setItem('presetList',JSON.stringify( list));
  }

}

export const downloadHandler = ()=>{
  const canvas = document.querySelector('#tryon-live-canvas-1');
  const dataURL = canvas.toDataURL("image/webp",0.2);
  const link = document.createElement("a");
  link.href = dataURL;
  const today = new Date();
  const d=today.getDate() ,s=today.getSeconds()   , min= today.getMinutes(),h=   today.getHours()  ,mon=   today.getMonth()       ;
  const filename = `makeup-${h}:${min}:${s}-${d}.${mon}.jpg`        ;
  // localStorage.setItem(filename, canvas.toDataURL());
  link.download = filename;
  link.click();
}
