export const  lastChangeSaver=({api,r,g,b,secondParam})=>{
  window.lastChanges =  () => {
      window.VmtHelper['remove' + api]();
      window.VmtHelper['apply' + api](r, g, b, window.makeUpAlpha, secondParam);
    }
}
export const  presetSaver=({api,r,g,b,secondParam})=>{
  window.presetSaver = {
    ...window.presetSaver, [api]:   {name:api,
     r, g, b,   secondParam
    }
  }
}