import * as React from 'react';
import {styles} from './bottomTabs.module.scss';
import {makeUpList} from "../../../config";

export const BottomTabs = ({setColorTabs ,colorTabs}) => {

  return (
    <div className={styles}>
      {makeUpList.map( (list ,i) =>{
        return <button   className={colorTabs===i? 'active':''} onClick={()=>{setColorTabs(i) }} key={i}>
          <svg width="20" height="21" viewBox="0 0 20 21" fill="none">
            <path d="M7 10.25C6.31 10.25 5.75 10.81 5.75 11.5C5.75 12.19 6.31 12.75 7 12.75C7.69 12.75 8.25 12.19 8.25 11.5C8.25 10.81 7.69 10.25 7 10.25ZM13 10.25C12.31 10.25 11.75 10.81 11.75 11.5C11.75 12.19 12.31 12.75 13 12.75C13.69 12.75 14.25 12.19 14.25 11.5C14.25 10.81 13.69 10.25 13 10.25ZM10 0.5C4.48 0.5 0 4.98 0 10.5C0 16.02 4.48 20.5 10 20.5C15.52 20.5 20 16.02 20 10.5C20 4.98 15.52 0.5 10 0.5ZM10 18.5C5.59 18.5 2 14.91 2 10.5C2 10.21 2.02 9.92 2.05 9.64C4.41 8.59 6.28 6.66 7.26 4.27C9.07 6.83 12.05 8.5 15.42 8.5C16.2 8.5 16.95 8.41 17.67 8.24C17.88 8.95 18 9.71 18 10.5C18 14.91 14.41 18.5 10 18.5Z" fill="white"/>
          </svg>
        {list.name}</button>
      })}
    </div>
  );
};
