import * as React from 'react';
import {styles} from './controls.module.scss';

import {useState} from "react";
import {Colors} from "../colors/colors";
import {BottomTabs} from "../bottomTabs/bottomTabs";

import {makeUpList, PRESETS} from "../../../config";
import {PresetLine} from "../presetLine/presetline";
import {InputRange} from "./inputRange";
import { BottomFunction } from '../bottomFunction/bottomFunction';

export const Controls = ({showCompare ,setShowCompare ,typePhoto}) => {
  const list = JSON.parse(window.localStorage.getItem('presetList'))||[...PRESETS]
  const [presetState ,setPresetState] = useState(list);
  const [leftTab , setLeftTab]=useState(true)
  const [colorTabs , setColorTabs] = useState(0);

  return (
    <div className={styles}>
      <div className="top">
        <button className={ leftTab ? "active": ""} onClick={()=>{setLeftTab(true)}}>
            Try Products
        </button>
        <button className={ leftTab ? "": "active"}  onClick={()=>{setLeftTab(false)}}>
            Try Full Looks
        </button>
        <div className={ leftTab ?"top-bgc" : "top-bgc right"} >

        </div>
      </div>
      <InputRange  />
      <div className="bottom">
          <BottomFunction  typePhoto={typePhoto} showCompare={showCompare} setPresetState={setPresetState}  setShowCompare={setShowCompare} />
        {leftTab && <BottomTabs colorTabs={colorTabs} setColorTabs={setColorTabs}/>}
        {leftTab && makeUpList.map((line , i)=>{
          return <div  key={i}  style={i===colorTabs?{}:{display:'none'}} >  <Colors   api={line.api}   colors={line.colors}/> </div>
        })}
        {!leftTab&&   <PresetLine presetState={presetState} setPresetState={setPresetState} />}
      </div>
    </div>
  );
};
