import * as React from 'react';
import {useMemo, useState} from 'react';
import {styles} from './compare.module.scss';
import {Canvas} from "./canvas";

export const Compare = ({showCompare}) => {

  const [moving, setMoving] = useState(0);
  const startPoint = window.innerWidth / 2;
  const touchHandler = (e) => {
    setMoving(e.changedTouches[0].clientX - startPoint);
  }
  const frame = 50;
  let framed = moving < frame - startPoint ? frame - startPoint : moving;
  framed = framed > startPoint - frame ? startPoint - frame : framed;
  const CanvMemo0 = useMemo(() => {
    return <Canvas id={'tryon-live-canvas-0'}/>
  }, [])
  const CanvMemo1 = useMemo(() => {
    return <Canvas id={'tryon-live-canvas-1'}/>
  }, [])
  return (
    <div className={styles}>
      <div className={'canvas-wrap'}>
        {showCompare &&
        <button style={{transform: 'translateX(' + framed + 'px )'}} onTouchMove={touchHandler} className={'move'}
                title='move to compare'>
          move to compare
          <span> </span>
        </button>}
        <div style={{width: `calc(50% +  ${framed}px)`, display: showCompare ? 'block' : 'none'}} className="noMakeUp">
          {CanvMemo0}
        </div>
        {CanvMemo1}
      </div>

    </div>
  );
};
