import * as React from 'react';
import {styles} from './bottomFunction.module.scss';
import {
  downloadHandler,
  liveZoomIn,
  liveZoomOut,
  photoZoomIn,
  photoZoomOut,
  savePresetHandler
} from "./handlers";
import {Icons } from "./icons";

export const BottomFunction = ({showCompare ,setShowCompare ,setPresetState,typePhoto=false}) => {

        const zoomIn = typePhoto? photoZoomIn : liveZoomIn;
        const zoomOut = typePhoto? photoZoomOut : liveZoomOut;
  return (
    <div className={styles}>
      <div className="wrap">
        <button onClick={()=>{window.lastChanges=()=>{} ; window.VmtHelper.clearMakeup()}}>
          {Icons.undo}
          undo
        </button>
        <button  onClick={zoomIn}>
          {Icons.zoomIn}
          zoom in
        </button>
        <button  onClick={zoomOut}>
          {Icons.zoomOut}
          zoom out
        </button>
        <button onClick={ ()=>{setShowCompare(!showCompare)} } title={'compare'} className= {showCompare?'active':''}>
          {Icons.compare}
          compare
        </button>

        <button onClick={downloadHandler}  title={'download'}>
          {Icons.download1}
          download
        </button>
        <button onClick={()=>{savePresetHandler(setPresetState) }  }  title={'download'}>
          {Icons.download2}
          save preset
        </button>
      </div>
    </div>
  );
};
